import Torus from "@toruslabs/torus-embed";
import "./App.css";
import { ethers, BigNumber } from "ethers";
import { useEffect, useState } from "react";
import VegasONE from "./abi/VegasONE.json";
import { isEmpty } from "lodash";

// 用來測試是否有的 token
const ERC20 = "0x00b8a4698970ad1d724360804E2864785A4BB1A5";

const getAddress = async (
  provider,
  web3auth,
  setSigner,
  setAddress,
  setUser,
  setBalanceOfMYOC
) => {
  let signer = provider.getSigner();
  setSigner(signer);

  let address = await signer.getAddress();
  setAddress(address);

  let user = await web3auth.getUserInfo();
  setUser(user);

  let contract = new ethers.Contract(ERC20, VegasONE["abi"], signer);

  let myBalance = await contract.balanceOf(address);
  setBalanceOfMYOC(
    BigNumber.from(myBalance).div(BigNumber.from(10).pow(18)).toString()
  );
};

function App() {
  const [web3auth, setWeb3auth] = useState();
  const [provider, setProvider] = useState();
  const [signer, setSigner] = useState();
  const [user, setUser] = useState({});
  const [address, setAddress] = useState("");
  const [balanceOfMYOC, setBalanceOfMYOC] = useState(0);
  const [value, setValue] = useState();
  const [to, setTo] = useState();

  useEffect(() => {
    const init = async () => {
      try {
        const torus = new Torus();
        // doc for parameters: https://docs.tor.us/wallet/api-reference/class
        await torus.init({
          // buildEnv: "production",
          // enableLogging: true,
          network: {
            host: "https://testnet-rpc.vegas.one",
            chainId: 1172,
          },
          showTorusButton: false,
        });
        console.log(torus);
        setWeb3auth(torus);
      } catch (err) {
        console.log("init web3auth", err);
      }
    };

    init();
  }, []);

  useEffect(() => {
    const getSigner = async () => {
      let ethProvider = new ethers.providers.Web3Provider(web3auth.provider);
      setProvider(ethProvider);

      if (web3auth.isLoggedIn) {
        getAddress(
          ethProvider,
          web3auth,
          setSigner,
          setAddress,
          setUser,
          setBalanceOfMYOC
        );
      }
    };
    if (web3auth) {
      getSigner();
    }
  }, [web3auth]);

  const login = async () => {
    await web3auth.login(); // await torus.ethereum.enable()
    getAddress(
      provider,
      web3auth,
      setSigner,
      setAddress,
      setUser,
      setBalanceOfMYOC
    );
  };

  const logout = async () => {
    if (!web3auth) {
      console.log("web3auth not initialized yet");
      return;
    }
    await web3auth.logout();
    setUser(null);
    setSigner(null);
    setAddress("");
    setBalanceOfMYOC(0);
  };

  const transfer = async () => {
    if (!provider) {
      console.log("provider not initialized yet");
      return;
    }

    const contract = new ethers.Contract(ERC20, VegasONE["abi"], signer);

    let tx = await contract.transfer(to, ethers.utils.parseEther(value), {
      gasLimit: 1e6,
    });

    await tx.wait();

    // refresh my balance
    let myBalance = await contract.balanceOf(address);
    setBalanceOfMYOC(
      BigNumber.from(myBalance).div(BigNumber.from(10).pow(18)).toString()
    );
  };

  return (
    <div className="App">
      <div>
        {isEmpty(address) && <button onClick={login}>login</button>}
        {!isEmpty(address) && <button onClick={logout}>logout</button>}
        <p>user: {!isEmpty(user) && user.name}</p>
        <p>address: {address}</p>
        <p>balance of TestCoin: {balanceOfMYOC}</p>
      </div>
      <div className="transfer">
        <div className="input-group">
          <p>to address: </p>
          <input type="text" onChange={(e) => setTo(e.target.value)} />
        </div>
        <div className="input-group">
          <p>amount: </p>
          <input type="text" onChange={(e) => setValue(e.target.value)} />
        </div>
        <button onClick={transfer}>transfer</button>
      </div>
    </div>
  );
}

export default App;
